.CategoryContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.CategoryContainer:nth-child(odd) {
  background-color: gray;
}

.CategoryContainer:nth-child(even) {
  background-color: gainsboro;
}

.main {
  display: flex;
  flex-direction: row;
  gap: 20px;
  /* margin: 1rem; */
  width: 95%;
}

.footer {
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 400px) {
  .main {
    margin: 0;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
  }
}
